export default {
    name: "settle-detail",
    components: {},
    data() {
        return {
            page: 1,
            args: false,
            load: false,
            mch_id: -1,
            is_transfer: 0,
            list: [],
        }
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.mch_id = options.mch_id;
        this.is_transfer = options.is_transfer;

        uni.setNavigationBarTitle({title: this.is_transfer === `1` ? `已结算金额` : `未结算金额`});
        this.loadData();
    },
    onReachBottom: function () {
        const self = this;
        if (self.args || self.load)
            return;
        self.load = true;
        let page = self.page + 1;

        self.$request({
            url: self.$api.mch.cash_log,
            data: {
                is_transfer: self.is_transfer,
                mch_id: self.mch_id,
                page: page,
            }
        }).then(info => {
            if (info.code === 0) {
                [self.page, self.args, self.list] = [page, info.data.list.length === 0, self.list.concat(info.data.list)];
            }
            self.load = false;
        });
    },

    methods: {
        loadData() {
            const self = this;
            self.$showLoading();
            self.$request({
                url: self.$api.mch.order_close_log,
                data: {
                    is_transfer: self.is_transfer,
                    mch_id: self.mch_id
                }
            }).then(info => {
                self.$hideLoading();
                self.list = info.data.list;
            }).catch(e => {
                self.$hideLoading();
            })
        },
    }
}
